import React from "react";
import DataTable from "react-data-table-component";
import Constants, { userToken } from "src/Constants";
import ActionButton from "./ActionButton";
import { CCard, CCardBody} from "@coreui/react";
import { ToastContainer, toast } from 'react-toastify';
import { Link, useNavigate } from "react-router-dom";
import UserPermissions from "src/hooks/Permissions";
import StatusButton from "./StatusButton";

function customSortable(key, rowA, rowB) {
    if(String(rowA[key]).toLocaleLowerCase().replace(/\W/g, '') < String(rowB[key]).toLocaleLowerCase().replace(/\W/g, '')) {
        return -1
    }
    if(String(rowA[key]).toLocaleLowerCase().replace(/\W/g, '') > String(rowB[key]).toLocaleLowerCase().replace(/\W/g, '')) {
        return 1
    }

    return 0;
}
const CameraTable = () => {

    const navigate = useNavigate();
    const {hasPerm} = UserPermissions();

    const [data, setData] = React.useState([]);
    const [pagination, setPagination] = React.useState({
        per_page: 100,
        page_number: 1
    })
    const [load, setLoad] = React.useState(true)
    const [total, setTotal] = React.useState(0);
    const handleChange = () => {

    }
    const columns = [
        {
            name: 'Display Name',
            selector: row => (hasPerm('feed_camera')) ? <Link to={`/camera-details/${row.id}`} className='fw-bold link-dark text-decoration-none'>{row.name}</Link> : <b>{row.name}</b>,
            sortable: true,
            sortFunction: (rowA, rowB) =>  customSortable('name', rowA, rowB)
            
        },
        {
            name: 'Jurisdiction',
            selector: row => <span className="text-uppercase">{row.jurisdiction}</span>,
            sortable: true,
            sortFunction: (rowA, rowB) =>  customSortable('jurisdiction', rowA, rowB)
        },
        {
            name: 'Address',
            selector: row => row.address,
            sortable: true,
            sortFunction: (rowA, rowB) =>  customSortable('address', rowA, rowB)
        },

        {
            name: 'Id',
            selector: row => row.unique_id,
            sortable: true,
            sortFunction: (rowA, rowB) =>  customSortable('unique_id', rowA, rowB)
        },
        {
            name: 'Display Status',
            selector: row => <StatusButton displayStatus={row.display_status}/>,
            sortable: true,
            sortFunction: (rowA, rowB) =>  customSortable('display_status', rowA, rowB)
        },
        {
            name: 'Last Modified',
            selector: row => new Date(row.updated_at).toLocaleString(),
            sortable: true,
            sortFunction: (rowA, rowB) =>  customSortable('updated_at', rowA, rowB)
        },
        {
            name: 'Action',
            selector: (row) => <ActionButton prefix="camera" onEdit={() => handleEdit(row)} onDelete={() => handleDelete(row.id)} />,
            sortable: false
        }
    ];

    // const handleView = (row) => {
    //     navigate('/cameras/view', { state: row });
    // }

    const handleEdit = (row) => {
        // console.log(row);
        navigate('/cameras/edit', { state: row });
    }

    const handleDelete = async (id) => {
        let confirmation  = window.confirm("Do you really want to delete this record?");
        if(!confirmation) {
            return;
        }
        let url = Constants.deleteCameras + id;
        let response = await fetch(url, {
            method: 'GET',
            headers: {
                'Authorization': userToken(),

            }
        })
        let json = await response.json();
        if (json.status === 200) {
            setLoad(!load);
            toast.success("Record deleted successfully!");
        }
    }

    React.useEffect(() => {
        let { sessionStorage } = window;
        if( sessionStorage.type && sessionStorage.message) {
            toast[sessionStorage.type](sessionStorage.message)
            sessionStorage.clear()
        }
    }, [])

    React.useEffect(() => {
        let url = Constants.cameras;


        (async () => {
            let response = await fetch(url + `?per_page=${pagination.per_page}&page_number=${pagination.page_number}&total_images=0`, {
                method: 'GET',
                headers: {
                    'Authorization': userToken(),

                }
            })
            let data = await response.json();
            setData(data.attributes
                );
            setTotal(data.totalResult);


        })()
    }, [pagination, load] )


    return (
        <>
            <ToastContainer
                position="top-right"
                theme="colored"
                autoClose={5000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover />

                
            <CCard className="my-2">
                <CCardBody>
                    <div className="row">
                        <div className="col-md-6">
                            <h5 className="text-uppercase">All Cameras</h5>
                        </div>
                        <div className="col-md-6">
                        <Link to="/cameras/add" className="btn text-uppercase btn-primary m-2 float-end">Add</Link>
                        </div>
                    </div>
                </CCardBody>
            </CCard>
            <DataTable
                columns={columns}
                data={data}
                pagination
                onSelectedRowsChange={handleChange}
                paginationServer
                paginationTotalRows={total}
                paginationPerPage={100}
                paginationRowsPerPageOptions={[100, 50, 30]}
                defaultSortFieldId={1}
                className='camera-table'
                onChangeRowsPerPage={(per_page) => setPagination({ ...pagination, per_page })}
                onChangePage={(page_number, per_page) => setPagination({ ...pagination, page_number })}
            />
        </>
    )
}

export default CameraTable;