import { useEffect, useState } from "react"

const UserPermissions = () => {
    const [perm, setperm] = useState([]);

    useEffect(() => {
       let permissions = window.localStorage.getItem('permissions');
       setperm(JSON.parse(permissions || '[]'))
    }, [])
    const setPerm = (permissions) => {
        window.localStorage.setItem('permissions', JSON.stringify(permissions))
        setperm(permissions)
    }

    const hasPerm = (permission) => {
        return perm.includes(permission)
    }

    return {setPerm, hasPerm}
}

export default UserPermissions;
