import React from 'react';
const StatusButton = (props) => {
    return (
        <>
         {
            props.displayStatus ? <span className="text-success text-center"><i className="fa-solid fa-circle-check"></i></span> : <span className="text-danger text-center"><i className="fa-solid fa-circle-xmark"></i></span>
         }
            
            
        </>
    )
}

export default StatusButton;